<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div style="margin: 20px;"></div>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-date-picker
          v-model="date"
          size="small"
          type="daterange"
          value-format="yyyy-MM-dd"
          unlink-panels
          range-separator=""
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <div id="echarts_box" style="width: 90%;height: 400px"></div>
    <div style="margin: 20px;"></div>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column prop="project_id" label="id" v-if=false>
      </el-table-column>
      <el-table-column sortable prop="projectName" label="" min-width="150">
      </el-table-column>
      <el-table-column sortable prop="submitted_test_director" label="" min-width="150">
      </el-table-column>
      <el-table-column sortable prop="submittedTest_num" label="" min-width="150">
      </el-table-column>
      <el-table-column sortable prop="smokeTesting_pass_num" label="" min-width="150">
      </el-table-column>
      <el-table-column sortable prop="smokeTesting_pass_rate" label="" min-width="150">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { smokeTestingRankReport } from '../../api/submittedTestsApi'
import echarts from 'echarts'
export default {
  data() {
    return {
      date: '',
      listData: ''
    }
  },
  methods: {
    // 
    search() {
      let param = {
        start_date: this.date[0],
        end_date: this.date[1]
      };
      smokeTestingRankReport(param)
        .then(res => {
            this.loading = false;
            if (res.success === false) {
              this.$message({
                type: 'info',
                message: res.msg
              })
            } else {
              this.listData = res.data;
              this.showChart(res.data);
              // %
              this.listData.forEach((item) => {
                item.smokeTesting_pass_rate = item.smokeTesting_pass_rate.toString() + '%'
              });
            }
          })
          .catch(err => {
            this.loading = false;
            console.log(err);
            this.$message.error('')
          })
    },
    // echarts
    showChart(data) {
      let myChart = echarts.init(document.getElementById('echarts_box'));
      let option = {
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            ['items', '', '', '']  // 
          ]
        },
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true, // 
            interval: 0, // 012
            rotate: -30, // -9090
            inside: false, // 
            margin: 14, // 
          }
        },
        yAxis: [
          {
            name: '%',
            type: 'value',
            min: 0,
            max: 100
          },
          {
            name: '',
            type: 'value',
            min: 0,
            max: 100
          }
        ],
        series: [{
          type: 'bar',
          yAxisIndex: 0
        }, {
          type: 'bar',
          yAxisIndex: 1
        }, {
          type: 'bar',
          yAxisIndex: 1
        }]
      };
      // datasource
      let max_num = 0; //y
      data.forEach(item => {
        let array = [item.projectName + '\n' + item.submitted_test_director, item.smokeTesting_pass_rate, item.submittedTest_num, item.smokeTesting_pass_num];
        option.dataset.source.push(array);
        if (item.submittedTest_num > max_num){
          max_num = item.submittedTest_num;
        }
      });
      let max_num_re = max_num % 5;
      max_num = max_num + (5 - max_num_re);
      option.yAxis[1].max = max_num;
      myChart.setOption(option);
    }
  }
}
</script>

<style scoped>

</style>



