<template>
    <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-form :inline="true" :model="formInline" class="user-search">
        <el-form-item label="">
          <el-autocomplete size="small" v-model="formInline.projectName" placeholder="" @select="handleSelect1" :fetch-suggestions="querySearchAsync1" @input="inputChange"></el-autocomplete>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="value" clearable placeholder="" @change="handleChange">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input size="small" v-model="formInline.apiName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="url">
          <el-input size="small" v-model="formInline.url" placeholder="url"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
          <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
          <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEnvironmentVariable()"></el-button>
        </el-form-item>
      </el-form>
      <!---->
      <el-row :gutter="20">
        <el-col :span="4">
          <el-card class="box-card;">
            <el-scrollbar style="height: 700px;overflow-wrap:break-word;" wrap-style="overflow-x:auto;">
              <el-input
                size="small"
                placeholder=""
                v-model="filterText">
              </el-input>
              <div style="margin: 20px;"></div>
              <el-tree
                class="filter-tree"
                :data="module_list"
                :props="defaultProps"
                node-key="id"
                default-expand-all
                :highlight-current="true"
                :filter-node-method="filterNode"
                @node-click="handleNodeClick"
                ref="tree">
              </el-tree>
            </el-scrollbar>
          </el-card>
        </el-col>
        <el-col :span="20">
          <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
            <el-table-column prop="id" label="api Id" v-if=false>
            </el-table-column>
            <el-table-column prop="apiModule_id" label="id" v-if=false>
            </el-table-column>
            <el-table-column prop="seq" label="" v-if=false>
            </el-table-column>
            <el-table-column sortable prop="module_name" label="" width="200">
            </el-table-column>
            <el-table-column sortable prop="api_name" label="" width="200">
            </el-table-column>
            <el-table-column sortable prop="request_method" label="" width="100">
            </el-table-column>
            <el-table-column sortable prop="url" label="url" width="250">
            </el-table-column>
            <el-table-column prop="independent" label="" width="150" align="center">
              <template scope="scope">
                <p v-if="scope.row.independent===false"></p>
                <p v-if="scope.row.independent===true"></p>
              </template>
            </el-table-column>
            <el-table-column sortable prop="summary" label="" width="250">
            </el-table-column>
            <el-table-column sortable prop="status" label="" width="75" align="center">
              <template slot-scope="scope">
                <el-switch
                  v-model="scope.row.status"
                  @change="changeStatus(scope.$index, scope.row)"
                  active-color="#13ce66"
                  inactive-color="#ff4949">
                </el-switch>
              </template>
            </el-table-column>
            <el-table-column align="center" label="" min-width="300" fixed="right">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="toTestCase(scope.row)"></el-button>
                <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
                <el-button size="mini" type="danger" @click="apiDelete(scope.$index, scope.row)"></el-button>
                <el-button-group>
                  <el-button size="mini" @click="upLayer(scope.$index, scope.row)"></el-button>
                  <el-button size="mini" @click="downLayer(scope.$index, scope.row)"></el-button>
                </el-button-group>
              </template>
            </el-table-column>
          </el-table>
          <!--  -->
          <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
        </el-col>
      </el-row>
      <!--  -->
      <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
        <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
          <el-form-item label="" prop="module_id">
            <el-select size="small" v-model="editForm.module_id" clearable placeholder="" @change="m_handleChange">
              <el-option
                v-for="item in m_options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="api_name">
            <el-input size="small" v-model="editForm.api_name" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="request_method">
            <el-select v-model="editForm.request_method" size="small" placeholder="">
              <el-option
                v-for="item in request_method_options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="url" prop="url">
            <el-input size="small" v-model="editForm.url" auto-complete="off" placeholder="url"></el-input>
          </el-form-item>
          <el-form-item label="" prop="independent">
            <el-radio-group v-model="editForm.independent">
              <el-radio :label=false></el-radio>
              <el-radio :label=true></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="summary">
            <el-input type="textarea" size="small" v-model="editForm.summary" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="environmentVariableVisible" width="55%" @click="closeDialog">
        <el-form :inline="true" :model="e_formInline" class="user-search">
          <el-form-item label="">
            <el-input size="small" v-model="e_formInline.ev_name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button size="small" type="primary" icon="el-icon-search" @click="handleEnvironmentVariable"></el-button>
            <el-button size="small" type="primary" icon="el-icon-plus" @click="e_handleEdit()"></el-button>
          </el-form-item>
        </el-form>
        <el-table size="small" :data="environmentVariableData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;margin-top: 20px">
          <el-table-column align="center" type="selection" width="60">
          </el-table-column>
          <el-table-column prop="id" label="id" v-if=false>
          </el-table-column>
          <el-table-column prop="e_id" label="id" v-if=false>
          </el-table-column>
          <el-table-column sortable prop="name" label="" width="300">
          </el-table-column>
          <el-table-column sortable prop="value" label="" width="300">
          </el-table-column>
          <el-table-column align="center" label="" min-width="300">
            <template slot-scope="scope">
              <el-button size="mini" @click="e_handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="EnvironmentVariableDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <Pagination v-bind:child-msg="e_pageparm" @callFather="e_callFather"></Pagination>
      </el-dialog>
      <!--  -->
      <el-dialog :title="e_title" :visible.sync="e_editFormVisible" width="30%" @click="e_editFormVisible = false">
        <el-form label-width="120px" :model="e_editForm" ref="e_editForm">
          <el-form-item label="" prop="name">
            <el-input size="small" v-model="e_editForm.name" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="value">
             <el-input size="small" v-model="e_editForm.value" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="e_editFormVisible = false"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="e_submitForm('e_editForm')"></el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
  import {getAllProject, projectModuleList, getAllProjectEnvironment} from '../../api/projectApi'
  import {apiList, deleteApi, deleteEnvironmentVariable, downApi, getEnvironmentVariable
    , addApi, editApi, addEnvironmentVariable, editEnvironmentVariable, upApi, changeApiStatus} from '../../api/apiTestApi'
  import Pagination from '../../components/Pagination'
  import { list_to_option, list_to_tree } from '../../utils/util'

  export default {
   name: "apiManagement",
   data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      environmentVariableVisible: false, //
      e_editFormVisible: false,
      title: '',
      e_title: '',
      editForm: {
        id: '',
        module_name: '',
        module_id: '',
        request_method: '',
        api_name:'',
        url: '',
        summary: '',
        independent: false
      },
      e_editForm: {
        id: '',
        e_id: '',
        name: '',
        value: ''
      },
      // rules
      rules: {
        module_id: [{ required: true, message: '', trigger: 'blur' }],
        request_method: [{ required: true, message: '', trigger: 'blur' }],
        api_name: [{ required: true, message: '', trigger: 'blur' }],
        url: [{ required: true, message: 'url', trigger: 'blur' }],
        independent: [{ required: true, message: '', trigger: 'blur' }]
      },
      projects: '',
      state: '',
      api_modules: '',
      module_list: '',
      defaultProps: {
        children: 'children',
        label: 'module_name'
      },
      current_module_id: '',
      filterText: '',
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        projectName:'',
        projectEnvironment_id: '',
        projectEnvironment_name: '',
        module_id: '',
        module_name: '',
        apiName: '',
        url: ''
      },
      e_formInline: {
        page: 1,
        limit: 10,
        projectEnvironment_id: '',
        ev_name: ''
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        oldPageSize: 10,
        total: 10
      },
      e_pageparm: {
        currentPage: 1,
        pageSize: 10,
        oldPageSize: 10,
        total: 10
      },
      // 
      options: '',
      value: '',
      // 
      m_options: '',
      request_method_options: [{
        value: 'GET',
        label: 'GET'
      },{
        value: 'POST',
        label: 'POST'
      },{
        value: 'PUT',
        label: 'PUT'
      },{
        value: 'DELETE',
        label: 'DELETE'
      }],
      environmentVariableData: []
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */
  created() {
    this.loadAllProject();
  },
  /**
   * 
   */
  methods: {
    // 
    getModuleData(parameter) {
      parameter = {
        projectEnvironment_id : this.formInline.projectEnvironment_id
      };
      projectModuleList(parameter)
        .then(res => {
          if (res.success === false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.originData = res.data;
            if (res.data !== []) {
              this.m_options = list_to_option(this.originData);
              this.module_list = list_to_tree(this.originData);
            }
          }
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message.error('')
        })
    },
    // api
    getdata(parameter) {
      this.loading = true;
      this.pageparm.currentPage = this.formInline.page;
      this.pageparm.pageSize = this.formInline.limit;
      parameter = {
        currentPage: this.pageparm.currentPage,
        pageSize: this.pageparm.pageSize,
        projectEnvironmentId: this.formInline.projectEnvironment_id,
        apiModuleId: this.formInline.module_id,
        apiName: this.formInline.apiName,
        url: this.formInline.url
      };
      /***
       *  
       */
      apiList(parameter)
        .then(res => {
          this.loading = false;
          if (res.success === false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data;
            this.pageparm.total = res.count;
          }
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      if(this.pageparm.pageSize !== parm.pageSize){
        this.formInline.page = 1;
        this.formInline.limit = parm.pageSize;
        this.getdata(this.formInline)
      }else {
        this.formInline.page = parm.currentPage;
        this.formInline.limit = parm.pageSize;
        this.getdata(this.formInline)
      }
    },
    // 
    e_callFather(parm) {
      if(this.e_pageparm.pageSize !== parm.pageSize){
        this.e_formInline.page = 1;
        this.e_formInline.limit = parm.pageSize;
        this.handleEnvironmentVariable()
      }else {
        this.e_formInline.page = parm.currentPage;
        this.e_formInline.limit = parm.pageSize;
        this.handleEnvironmentVariable()
      }
    },
    // 
    search() {
      this.formInline.page = 1;
      this.formInline.module_id = '';
      this.getModuleData(this.formInline);
      this.getdata(this.formInline);
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      if (row != undefined && row != 'undefined') {
        this.title = '';
        this.editForm.id = row.id;
        this.editForm.module_id = row.apiModule_id;
        this.editForm.request_method = row.request_method;
        this.editForm.api_name = row.api_name;
        this.editForm.url = row.url;
        this.editForm.independent = row.independent;
        this.editForm.summary = row.summary;
      } else {
        this.title = '';
        this.editForm.id = '';
        this.editForm.module_id = this.current_module_id;
        this.editForm.request_method = '';
        this.editForm.api_name = '';
        this.editForm.url = '';
        this.editForm.summary = '';
      }
    },
    e_handleEdit: function(index, row) {
      this.e_editFormVisible = true;
      if (row != undefined && row != 'undefined') {
        this.e_title = '';
        this.e_editForm.id = row.id;
        this.e_editForm.e_id = this.formInline.projectEnvironment_id;
        this.e_editForm.name = row.name;
        this.e_editForm.value = row.value;
      } else {
        this.e_title = '';
        this.e_editForm.id = '';
        this.e_editForm.e_id = this.formInline.projectEnvironment_id;
        this.e_editForm.name = '';
        this.e_editForm.value = '';
      }
    },
    //
    handleEnvironmentVariable() {
      this.e_formInline.projectEnvironment_id = this.formInline.projectEnvironment_id;
      this.e_pageparm.currentPage = this.e_formInline.page;
      this.e_pageparm.pageSize = this.e_formInline.limit;
      let param = {
        currentPage: this.e_pageparm.currentPage,
        pageSize: this.e_pageparm.pageSize,
        e_id: this.e_formInline.projectEnvironment_id,
        ev_name: this.e_formInline.ev_name
      };
      getEnvironmentVariable(param)
        .then(res => {
          if (res.success){
            this.environmentVariableData = res.data;
            this.environmentVariableVisible = true;
            this.e_pageparm.total = res.count;
          }else {
            this.$message({
              type: 'info',
              message: res.msg
            })
          }
        })
        .catch(err => {
          this.loading = false;
          this.$message.error('')
        });
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          if (this.editForm.id === ''){
            let param = this.editForm;
            delete param['id'];
            addApi(param)
              .then(res => {
                this.editFormVisible = false;
                this.loading = false;
                if (res.success) {
                  this.getdata(this.formInline);
                  this.$message({
                    type: 'success',
                    message: res.msg
                  })
                } else {
                  this.$message({
                    type: 'info',
                    message: res.msg
                  })
                }
              })
              .catch(err => {
                this.editFormVisible = false;
                this.loading = false;
                this.$message.error('')
              })
          } else {
            let param = this.editForm;
            editApi(param)
              .then(res => {
                this.editFormVisible = false;
                this.loading = false;
                if (res.success) {
                  this.getdata(this.formInline);
                  this.$message({
                    type: 'success',
                    message: res.msg
                  })
                } else {
                  this.$message({
                    type: 'info',
                    message: res.msg
                  })
                }
              })
              .catch(err => {
                this.editFormVisible = false;
                this.loading = false;
                this.$message.error('')
              })
          }
        } else {
          return false
        }
      })
    },
    e_submitForm(e_editData) {
      this.$refs[e_editData].validate(valid => {
        if (valid) {
          if (this.e_editForm.id === ''){
            let param = this.e_editForm;
            delete param['id'];
            addEnvironmentVariable(param)
              .then(res => {
                this.e_editFormVisible = false;
                this.loading = false;
                if (res.success) {
                  this.handleEnvironmentVariable();
                  this.$message({
                    type: 'success',
                    message: res.msg
                  })
                } else {
                  this.$message({
                    type: 'info',
                    message: res.msg
                  })
                }
              })
              .catch(err => {
                this.editFormVisible = false;
                this.loading = false;
                this.$message.error('')
              })
          } else {
            let param = this.e_editForm;
            editEnvironmentVariable(param)
              .then(res => {
                this.e_editFormVisible = false;
                this.loading = false;
                if (res.success) {
                  this.handleEnvironmentVariable();
                  this.$message({
                    type: 'success',
                    message: res.msg
                  })
                } else {
                  this.$message({
                    type: 'info',
                    message: res.msg
                  })
                }
              })
              .catch(err => {
                this.editFormVisible = false;
                this.loading = false;
                this.$message.error('')
              })
          }
        } else {
          return false
        }
      })
    },
    // 
    closeDialog() {
      this.editFormVisible = false;
    },
    // api
    apiDelete(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          let parameter = {
            id: row.id,
            seq: row.seq,
            apiModule_id: row.apiModule_id
          };
          deleteApi(parameter)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: res.msg
                });
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              console.log(err);
              this.loading = false;
              this.$message.error('')
            })
        })
        .catch((err) => {
          console.log(err)
        })
    },
    // 
    EnvironmentVariableDelete(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          let parameter = {
            id: row.id
          };
          deleteEnvironmentVariable(parameter)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: res.msg
                });
                this.handleEnvironmentVariable()
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              console.log(err);
              this.loading = false;
              this.$message.error('')
            })
        })
        .catch((err) => {
          console.log(err)
        })
    },
    // 
    toTestCase(row){
      // // cookie
      this.$router.push({
        path: '/apiTest/apiManagement/apiTestCase',
        query: {
          api_id: row.id
        }
      })
    },
    // 
    loadAllProject(){
      getAllProject().then(
        res => {
          this.projects = res;
        }
      )
    },
    querySearchAsync1(queryString, cb) {
      var projects = this.projects;
      var results = queryString ? projects.filter(this.createStateFilter(queryString)) : projects;

      cb(results);
    },
    createStateFilter(queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect1(item) {
      this.options = '';
      this.value = '';
      this.loadAllProjectEnvironment(item);
      this.formInline.module_id = '';
      this.formInline.module_name = '';
    },
    inputEvent(){
      if(this.formInline.module_name === ''){
        this.formInline.module_id = '';
        this.editForm.module_id = '';
        this.editForm.module_name = '';
      }
    },
    // 
    loadAllProjectEnvironment(item){
      var params = {
        name: item.value
      };
      getAllProjectEnvironment(params).then(
        res => {
          this.options = res;
        }
      )
    },
    // api
    handleChange(val){
      this.formInline.module_name = '';
      this.formInline.module_id = '';
      this.formInline.projectEnvironment_id = val;
    },
    //
    inputChange(){
      this.formInline.projectEnvironment_id = '';
      this.formInline.projectEnvironment_name = '';
      this.options = '';
      this.value = '';
      this.formInline.module_id = '';
      this.formInline.module_name = '';
    },
    m_handleChange(val){
      this.editForm.module_id = val;
    },
    //
    upLayer(index, row){
      let that = this;
      if (index === 0) {
        that.$message({
          message: "",
          type: "warning"
        });
      } else {
        let params = {
          apiModule_id: row.apiModule_id,
          seq: row.seq,
          form_apiModule_name: this.formInline.module_name
        };
        upApi(params)
          .then(res => {
            if (res.success === false) {
              this.$message({
                type: 'info',
                message: res.msg
              })
          } else {
              this.getdata(this.formInline);
              let upDate = that.listData[index - 1];
              that.listData.splice(index - 1, 1);
              that.listData.splice(index, 0, upDate);
          }
          });
      }
    },
    //
    downLayer(index, row){
      let that = this;
      if (index + 1 === that.listData.length) {
        that.$message({
          message: "",
          type: "warning"
        });
      } else {
        let params = {
          apiModule_id: row.apiModule_id,
          seq: row.seq,
          form_apiModule_name: this.formInline.module_name
        };
        downApi(params)
          .then(res => {
            if (res.success === false) {
              this.$message({
                type: 'info',
                message: res.msg
              })
          } else {
              this.getdata(this.formInline);
              let downDate = that.listData[index + 1];
              that.listData.splice(index + 1, 1);
              that.listData.splice(index, 0, downDate);
          }
        });
      }
    },
    //
    changeStatus(index, row){
      let params = {
        id: row.id,
        status: row.status
      };
      changeApiStatus(params)
        .then(res => {
          if (res.success === false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          }
        })
    },
    closeEnvironmentVariableDialog(){
      this.environmentVariableVisible = false;
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.module_name.indexOf(value) !== -1;
    },
    handleNodeClick(data) {
      console.log(data);
      this.formInline.module_id = data.id;
      this.formInline.module_name = data.module_name;
      this.current_module_id = data.id;
      this.getdata(this.formInline);
    },
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
/deep/ textarea {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif !important;
}
.add-table {
  height: 50px;
  line-height: 50px;
  border-left: 1px solid #EBEEF5;
  border-right: 1px solid #EBEEF5;
  border-bottom: 1px solid #EBEEF5;
  color: dodgerblue;
}
</style>



