<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div style="margin: 20px;"></div>
    <!--<div id="echarts_box_EEP" style="width: 90%;height: 400px"></div>-->
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-date-picker
          v-model="date"
          size="small"
          type="daterange"
          value-format="yyyy-MM-dd"
          unlink-panels
          range-separator=""
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-select size="small" v-model="dimension_value" placeholder="">
          <el-option
            v-for="item in dimension_options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <div v-for="(value,key,index) in list" ref="list">
      <el-card class="box-card;">
        <!--  -->
        <div style="font-size: 20px;font-family: Avenir,Helvetica,Arial,sans-serif;">{{key}}</div>
        <div style="margin-top: 20px;"></div>
        <div :id="'echarts_box_'+ key" style="width: 90%;height: 360px"></div>
        <!---->
        <el-table size="small" :data="value" v-loading="loading" :span-method="objectSpanMethod" border element-loading-text="" style="width: 100%;">
          <el-table-column prop="project_id" label="id" v-if=false>
          </el-table-column>
          <el-table-column prop="rowspan" label="rowspan" v-if=false>
          </el-table-column>
          <el-table-column sortable prop="smoke_testing_fail_reason_category" label="" min-width="150">
          </el-table-column>
          <el-table-column sortable prop="smoke_testing_fail_reason_detail" label="" min-width="150">
          </el-table-column>
          <el-table-column sortable prop="smoke_testing_fail_reason_detail_num" label="" min-width="150">
          </el-table-column>
          <el-table-column sortable prop="detail_rate" label="" min-width="150">
          </el-table-column>
          <el-table-column sortable prop="category_rate" label="" min-width="150">
          </el-table-column>
        </el-table>
      </el-card>
      <div style="margin-top: 20px;"></div>
    </div>
  </div>
</template>

<script>
import { smokeTestingFailReasonAnalysisReport } from '../../api/submittedTestsApi'
import echarts from 'echarts'
export default {
  data() {
    return {
      date: '',
      dimension_value: '1',
      dimension_options: [{
        value: '1',
        label: ''
      },{
        value: '2',
        label: ''
      }],
      list: '',
      categoryArr:[], //
      categoryPos: 0  //
    }
  },
  watch: {
    list:function () {
      this.$nextTick(function () {
        this.showChart(this.list);
      })
    }
  },
  mounted() {
    this.showChart();
  },
  methods: {
    // 
    search() {
      let param = {
        start_date: this.date[0],
        end_date: this.date[1],
        dimension: this.dimension_value
      };
      smokeTestingFailReasonAnalysisReport(param)
        .then(res => {
            this.loading = false;
            if (res.success === false) {
              this.$message({
                type: 'info',
                message: res.msg
              })
            } else {
              this.list = res.data;
            }
          })
          .catch(err => {
            this.loading = false;
            console.log(err);
            this.$message.error('')
          });
    },
    // echarts
    showChart(data){
      // 
      for(let key in data){
        let myChart = echarts.init(document.getElementById('echarts_box_' + key.toString()));
        let option = {
          title: {
            text: '',
            left: 'center'
          },
          tooltip: {
            trigger: 'item'
          },
          legend:{
            orient: 'vertical',
            left: 'left'
          },
          series: [
            {
              name: '',
              type: 'pie',
              radius: '50%',
              center: ['50%', '50%'],
              data: [],
              itemStyle: {
                normal: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              },
              animationType: 'scale',
              animationEasing: 'elasticOut',
              animationDelay: function (idx) {
                return Math.random() * 200;
              }
            }
          ]
        };
        // optiondata
        data[key].forEach(item => {
           let dic = { value: item.smoke_testing_fail_reason_detail_num, name: item.smoke_testing_fail_reason_detail };
           option.series[0].data.push(dic);
        });
        // console.log(option);
        myChart.setOption(option, true);
      }
    },
    // mergeInit() {
    //     this.categoryArr = [];
    //     this.categoryPos = 0;
    // },
    // merge(data) {
    //   this.mergeInit();
    //   if (data.length > 0) {
    //     for (let i = 0; i < data.len(); i++) {
    //       if (i === 0) {
    //         //
    //         this.categoryArr.push(1);
    //         this.categoryPos = 0;
    //       } else {
    //         // 
    //         if (data[i].smoke_testing_fail_reason_category === data[i - 1].smoke_testing_fail_reason_category){
    //           this.categoryArr[this.categoryPos] += 1;
    //           this.categoryArr.push(0);
    //         } else {
    //           this.categoryArr.push(1);
    //           this.categoryPos = i;
    //         }
    //       }
    //     }
    //   }
    // },
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        // 
        const row1 = row.rowspan;
        const col1 = row1 > 0 ? 1 : 0;
        return {
          rowspan: row1,
          colspan: col1
        };
      } else if (columnIndex === 4){
        const row1 = row.rowspan;
        const col1 = row1 > 0 ? 1 : 0;
        return {
          rowspan: row1,
          colspan: col1
        };
      } else {
        return {
          rowspan: 1,
          colspan: 1
        };
      }
    }
  }
}
</script>

<style scoped>

</style>



