/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.nickname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="userData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column prop="id" label="id" v-if=false>
      </el-table-column>
      <el-table-column prop="role" label="" v-if=false>
      </el-table-column>
      <el-table-column align="center" sortable prop="username" label="" width="200">
      </el-table-column>
      <el-table-column align="center" sortable prop="nickname" label="" width="200">
      </el-table-column>
      <el-table-column align="center" sortable prop="phone" label="" width="200">
      </el-table-column>
      <el-table-column align="center" sortable prop="email" label="" min-width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="status" label="" min-width="50">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.status=='1'?nshow:fshow" active-color="#13ce66" inactive-color="#ff4949" @change="editStatus(scope.$index, scope.row)">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="150">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" @click="getProjectPermissionList(scope.$index, scope.row)" v-if="scope.row.role === 'dev_role'"></el-button>
          <el-button size="mini" type="success" @click="resetpwd(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog()'>
      <el-form label-width="80px" ref="editForm" :model="editForm" :rules="rules">
        <el-form-item label="" prop="username">
          <el-input size="small" v-model="editForm.username" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickname">
          <el-input size="small" v-model="editForm.nickname" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="role_code">
          <el-select v-model="editForm.role_code" size="small" placeholder="">
            <el-option
              v-for="item in role_option"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userMobile">
          <el-input size="small" v-model="editForm.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="userEmail">
          <el-input size="small" v-model="editForm.email" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog()'></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="projectPermissionVisible" width="30%" @click='closeDialog()'>
      <el-table size="small"
                ref="projectPermissionTable"
                :data="projectPermissionData"
                highlight-current-row v-loading="loading"
                border element-loading-text=""
                @selection-change="(selection)=>{handleSelectChange(selection)}"
                style="width: 100%;">
        <el-table-column align="center" type="selection" width="50">
        </el-table-column>
        <el-table-column prop="project_id" label="id" v-if=false>
        </el-table-column>
        <el-table-column align="center" sortable prop="projectName" label="">
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click='projectPermissionsSave()'></el-button>
        <el-button size="small" @click='closeDialog()'></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 
import {
  userList,
  getRoleCode,
  addUser,
  editUser,
  userDelete,
  resetPwd,
  changeUserStatus,
  projectPermissionsList,
  projectPermissionsSave
} from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      title: '',
      editFormVisible: false, //
      projectPermissionVisible: false, //
      current_user_id: '',
      projectPermissionSelection: '',
      // 
      editForm: {
        id: '',
        username: '',
        nickname: '',
        role_code: '',
        phone: '',
        email: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      role_option: '',
      // rules
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        nickname: [
          { required: true, message: '', trigger: 'blur' }
        ],
        role_code: [{ required: true, message: '', trigger: 'blur' }],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1(3\d|47|5((?!4)\d)|7(0|1|[6-8])|8\d)\d{8,8}$/,
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // 
      formInline: {
        page: 1,
        limit: 10,
        nickname: '',
        isLock: ''
      },
      //
      userData: [],
      //
      projectPermissionData: [],
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },

  /**
   * 
   */
  watch: {},

  /**
   * 
   */
  created() {
    this.getdata(this.formInline);
    this.getRoleCode();
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      /***
       *  
       */
      this.loading = true;
      this.pageparm.currentPage = this.formInline.page;
      this.pageparm.pageSize = this.formInline.limit;
      parameter = {
        currentPage: this.pageparm.currentPage,
        pageSize: this.pageparm.pageSize,
        nickname : this.formInline.nickname
      };
      // 
      userList(parameter).then(res => {
        this.loading = false;
        if (res.success === false) {
          this.$message({
            type: 'info',
            message: res.msg
          })
        } else {
          this.userData = res.data;
          // 
          this.pageparm.currentPage = this.formInline.page;
          this.pageparm.pageSize = this.formInline.limit;
          this.pageparm.total = res.count;
        }
      })
    },
    // 
    callFather(param) {
      this.formInline.page = param.currentPage;
      this.formInline.limit = param.pageSize;
      this.getdata(this.formInline);
    },
    //
    search() {
      this.getdata(this.formInline)
    },
    // code
    getRoleCode(){
      getRoleCode().then(res => {
        this.role_option = res.data;
      })
    },
    // 
    closeDialog() {
      this.editFormVisible = false;
      this.projectPermissionVisible = false;
    },
    // 
    editStatus: function(index, row) {
      this.loading = true;
      let param = {
        status: '',
        id: '',
      };
      param.id = row.id;
      let status = row.status;
      if (status === 2) {
        param.status = '1'
      } else {
        param.status = '2'
      }
      // 
      changeUserStatus(param).then(res => {
        this.loading = false;
        if (res.success === false) {
          this.$message({
            type: 'info',
            message: res.msg
          })
        } else {
          this.$message({
            type: 'success',
            message: ''
          });
          this.getdata(this.formInline)
        }
      })
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      if (row !== undefined && row !== 'undefined') {
        this.title = '';
        this.editForm.id = row.id;
        this.editForm.username = row.username;
        this.editForm.nickname = row.nickname;
        this.editForm.role_code = row.role;
        this.editForm.phone = row.phone;
        this.editForm.email = row.email;
      } else {
        this.title = '';
        this.editForm.id = '';
        this.editForm.username = '';
        this.editForm.nickname = '';
        this.editForm.role_code = '';
        this.editForm.phone = '';
        this.editForm.email = '';
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          if (this.editForm.id === ''){
            let param = this.editForm;
            delete param['id'];
            addUser(param)
              .then(res => {
                this.editFormVisible = false;
                this.loading = false;
                if (res.success) {
                  this.getdata(this.formInline);
                  this.$message({
                    type: 'success',
                    message: ''
                  })
                } else {
                  this.$message({
                    type: 'info',
                    message: res.msg
                  })
                }
              })
              .catch(err => {
                this.editFormVisible = false;
                this.loading = false;
                this.$message.error('')
              })
          } else {
            let param = this.editForm;
            editUser(param)
              .then(res => {
                this.editFormVisible = false;
                this.loading = false;
                if (res.success) {
                  this.getdata(this.formInline);
                  this.$message({
                    type: 'success',
                    message: ''
                  })
                } else {
                  this.$message({
                    type: 'info',
                    message: res.msg
                  })
                }
              })
              .catch(err => {
                this.editFormVisible = false;
                this.loading = false;
                this.$message.error('')
              })
          }
        } else {
          return false
        }
      })
    },
    // 
    deleteUser(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          let parameter = {
            id: row.id
          };
          // 
          userDelete(parameter)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: res.msg
                });
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false;
              this.$message.error('');
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    //
    getProjectPermissionList: function(index, row) {
      this.projectPermissionVisible = true;
      this.current_user_id = row.id;
      let param = {
        id: this.current_user_id
      };
      projectPermissionsList(param).then(res => {
          this.loading = false;
          if (res.success === false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.projectPermissionData = res.data;
            this.toggleSelection();
          }
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message.error('')
        });
    },
    toggleSelection(){
      this.$nextTick(() => {
        this.projectPermissionData.forEach(item => {
          if(item.user_id != null){
            this.$refs.projectPermissionTable.toggleRowSelection(item, true);
          }
        })
      });
    },
    // 
    handleSelectChange(selection){
      this.projectPermissionSelection = selection;
    },
    // 
    projectPermissionsSave(){
      this.projectPermissionSelection.forEach(item => {
        delete item.user_id;
      });
      let param = {
        id: this.current_user_id,
        list: this.projectPermissionSelection
      };
      projectPermissionsSave(param).then(res => {
          this.loading = false;
          if (res.success === false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.projectPermissionVisible = false;
            this.$message({
              type: 'success',
              message: ''
            })
          }
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message.error('')
        });
    },
    // 
    resetpwd(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          let param = {
            id: row.id
          };
          resetPwd(param)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: ''
                });
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false;
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>




